import React, { Component } from 'react';
import Text from '../Text/Text';
import './Edito.css';


export default class Edito extends Component {

    constructor(props) {
        super(props);
        this.state = {
            top: 'initial',
            bottom: 'initial',
            left: 'initial'
        };
    }

    componentDidMount() {
        if (this.props.positionTop) {
            this.setState({top: this.props.positionTop});
        }
        if (this.props.positionBottom) {
            this.setState({bottom: this.props.positionBottom});
        }
        if (this.props.largeur) {
            this.setState({maxwidth: this.props.largeur});
        }
        if (this.props.positionLeft) {
            this.setState({left: this.props.positionLeft});
        }
        if (this.props.positionRight) {
            this.setState({right: this.props.positionRight});
        }
        if (this.props.alignement) {
            this.setState({textalignement: this.props.alignement});
        }
    }

    render() {

        return (
            <div className="Edito">
                <div className="Edito__wrapper" style={{top: this.state.top, bottom: this.state.bottom, maxWidth: this.state.maxwidth, textAlign: this.state.textalignement, left: this.state.left, right: this.state.right}}>
                    <Text content={this.props.content}></Text>
                </div>
            </div>
        );
    }
}



