import React, { Component } from 'react';
import move from '../../js/move';
import Swipeable from 'react-swipeable';
import Audio from '../Audio/Audio';
import './Intro.css';


export default class Intro extends Component {

  constructor(props) {
      super(props);
      this.state = {maxheight: '85px', top: '100px', fond: '50%'};
    }

  handleClick = () =>{
    this.setState({maxheight: '160px', top: '25px', fond: '100%'});
    }

    render() {

      function swipeLeft() {
          move.next();
      }

        return (
            <div className="Intro">
                <div className="Introhaut">
                    <p>Pour partir  la dcouverte de Timothe Adolphe, alias le Gupard blanc, deux choix :</p>
                    <p>Suivre son parcours et sa prparation en images et en textes.</p>
                    <Swipeable onSwipedLeft={swipeLeft} onClick={swipeLeft}>
                      <button>Continuer la lecture</button>
                    </Swipeable>
                </div>
                <div className="Introbas" style={{height: this.state.fond}}>
                    <p>Ou, pour les non-voyants et les amoureux du podcast, lcouter parler de cette aventure qui l'amnera  Tokyo.</p>
                </div>
                <div className="Introlecture" onClick={this.handleClick} style={{maxHeight: this.state.maxheight, top: this.state.top}}>
                <div className="Introcontenu">
                <Audio title="Podcast" name="https://nicolasflry.github.io/explore2020/podcast.mp3" duration="33:16" audiomode="dark"></Audio>
                </div>
                </div>
            </div>

        );
    }
}



