import React, { Component } from 'react';
import utils from '../../js/utils.js';
import './Panneau.css';

export default class Panneau extends Component {

  constructor(props) {
    super(props);
    this.handleMouseHover = this.handleMouseHover.bind(this);
    this.state = {
      isHovering: false,
    };
  }

  handleMouseHover() {
    this.setState(this.toggleHoverState);
  }

  toggleHoverState(state) {
    return {
      isHovering: !state.isHovering,
    };
  }

  componentDidMount() {
      if (this.props.decalageHaut) {
          this.setState({top: this.props.decalageHaut});
      }
      if (this.props.decalageDroite) {
          this.setState({right: this.props.decalageDroite});
  }
  if (this.props.decalageGauche) {
    this.setState({left: this.props.decalageGauche});
  }
  }

    render() {
      var style = {top: this.state.top,left: this.state.left}
      var darkimg = {width: '100%', height: '100%', background: '#0a0a0a', opacity: '0', visibility: 'hidden', position: 'absolute', top: '0px', left: '0px'}
  		if (this.state.right) {
  			style = {top:this.state.top,right: this.state.right, left: 'auto'}
  		}
      if (this.state.isHovering) {
        darkimg = {width: '100%', height: '100%', background: '#0a0a0a', opacity: '0.9', visibility: 'visible', position: 'absolute', top: '0px', left: '0px',  transitionDelay: '0s'}
      }
      return (
        <div>
        <div className="fond" style={darkimg}></div>
        <div className={"Panneau Panneau--z" + this.state.index + " js-element-z" + this.state.index} style={style}>
          <div id={this.props.name} className={"Panneaupersonne"}
            onMouseEnter={this.handleMouseHover}
            onMouseLeave={this.handleMouseHover}
          >
            <img src={process.env.PUBLIC_URL + "/img/px.png"} data-src={require("./img/" + this.props.name + '.jpg')} alt="" className="js-lazy Panneauimage" />
          </div>
          {
            this.state.isHovering &&
            <div id={this.props.name + "desc"} className={"Panneaudescription"}>
              <p className="Panneaunom" dangerouslySetInnerHTML={{__html: utils.cleanText(this.props.pNom)}}></p>
              <p dangerouslySetInnerHTML={{__html: utils.cleanText(this.props.pDesc)}}></p>
            </div>
          }
        </div>
        </div>
      );
    }
  }



